.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#root .card .card-content{
  margin-top: 0;  
}

.main.container {
  position: relative;
}

.input-field {
  margin-top: 0;
}

.nav_box {
  margin: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}


.nav_box_top.card {
  -webkit-box-shadow: 0px 3px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
  border-radius: 2px 2px 0 0;
}

.nav_box_bottom.card {
  -webkit-box-shadow: 1px 3px 2px 0 rgba(0,0,0,0.14), -1px 4px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
          box-shadow: 1px 3px 2px 0 rgba(0,0,0,0.14), -1px 4px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
  border-radius: 0 0 2px 2px;
}

.nav_box_top.card .card-content .card-title{
  font-size: 2.28rem;
  font-weight: 400;
  margin-bottom: 0;
  line-height: inherit;
}

.nav_box_top.card .card-content {
  position: relative;
}

.preloader {  
  position: absolute;
  top: 32vh;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 100;
}

.nav_box .divider {
  margin-top: 38px;
}

#post-list {
  margin-bottom: 17vh;
}

#post-list h5 {  
  margin: 0;  
  width: 100%;
}

#post-list .collapsible-header {
  border-bottom: 2px solid #ddd;
  cursor: auto;
  padding-left: 1.7rem;
  -webkit-transition: background 0.15s ease-in-out, color 0.15s ease-in-out;
  -o-transition: background 0.15s ease-in-out, color 0.15s ease-in-out;
  transition: background 0.15s ease-in-out, color 0.15s ease-in-out;
}

#post-list .collapsible-header:hover {
  background: #26c6da;
  color: #fff;
}

input[type=text].valid:not(.browser-default), input[type=text]:not(.browser-default):focus:not([readonly]), input[type=text].valid:not(.browser-default):focus {
  border-bottom: 1px solid #00bcd4;
  -webkit-box-shadow: 0 1px 0 0 #00bcd4;
  box-shadow: 0 1px 0 0 #00bcd4;
}

input.invalid:not([type]):focus {
  border-bottom: 1px solid #F44336;
  -webkit-box-shadow: 0 1px 0 0 #F44336;
  box-shadow: 0 1px 0 0 #F44336;
}

input[type=text]:not(.browser-default):focus:not([readonly])+label {
  color:#00bcd4;  
}

.post-body-icons {  
  height: 40px;  
  -webkit-box-pack: end;  
      -ms-flex-pack: end;  
          justify-content: flex-end;
}

.post-body-icons i {
  cursor: pointer;  
}

.post-body-icons a {
  margin-right: 10px;
}

.collapsible-header .delete {
  opacity: 0;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.collapsible-header:hover .delete {
  opacity: 0.6;
}

.material-icons.delete {
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  color: #f44336; 
  margin-right: 0; 
}

.material-icons.delete:hover {
  opacity: 1;
}

#autocomplete-input {
  margin-bottom: 0;
}

#post-list .collapsible-body {
  -webkit-transition: color 0.15s ease-in-out;
  -o-transition: color 0.15s ease-in-out;
  transition: color 0.15s ease-in-out;
}

#post-list .collapsible-body:hover {
  color: #00bcd4;
}

@media screen and (min-width: 600px) {
  .collapsible-body {
    padding: 2rem 3.5rem 2rem 3.5rem;
  }
}

@media screen and (max-width: 800px) {
  .nav_box_bottom.card {
    position: relative;
    top: -1px;
  }
}

@media screen and (max-width: 600px) {
  #post-list h5 {
    font-size: 1.34rem;
  }
}